<template>
    <b-container class="bv-example-row">
        <b-row><b-col><input type="text" v-model="searchQuery" placeholder="Search..." class="search"></b-col><router-link to="/logout">Logout</router-link></b-row>
        <b-row v-if="!isMobile()">
            <b-col sm="5" class="scroll">
                <div v-for="(recipe,i) in resultQuery" :key="i" class="listitem" @click="selected = recipe">
                    <img v-if="recipe.image" v-bind:src="'data:image/png;base64,'+recipe.image" />
                    <img v-else width="44" height="44" src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mP8Xw8AAoMBgDTD2qgAAAAASUVORK5CYII=" /> {{recipe.title}}
                </div>
            </b-col>
            <b-col sm="7" v-if="selected.title" class="scroll">
                <b-button v-if="isMobile()" @click="selected = {}">Back</b-button>
                <h1>{{selected.title}}</h1>
                <p v-html="nl2br(selected.body)"></p>
            </b-col>
        </b-row>
        <b-row v-else>
            <b-col sm="4" class="scroll" v-if="!selected.title">
                <div v-for="(recipe,i) in resultQuery" :key="i" class="listitem" @click="selected = recipe">
                    <img v-if="recipe.image" v-bind:src="'data:image/png;base64,'+recipe.image" />
                    <img v-else width="44" height="44" /> {{recipe.title}}
                </div>
            </b-col>
            <b-col sm="8" v-if="selected.title" class="scroll">
                <b-button v-if="isMobile()" @click="selected = {}">Back</b-button>
                <h1>{{selected.title}}</h1>
                <p v-html="nl2br(selected.body)"></p>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
import UserInfoStore from '../app/user-info-store'
import axios from 'axios'

export default {
    name: 'Home',
    methods: {
        nl2br: function (c) {
            if (c[0] == '<') {
                return c
            }
            return c.replace(/\n+/g, "<br/>\n")
        },
        isMobile() {
            if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return true
            } else {
                return false
            }
        },
    },
    computed: {
        resultQuery() {
            if (this.searchQuery) {
                return this.recipes.filter((item) => {
                    return this.searchQuery.toLowerCase().split(' ').every(v => item.title.toLowerCase().includes(v) || item.body.toLowerCase().includes(v))
                })
            } else {
                return this.recipes;
            }
        }
    },
    data() {
        return {
            userInfo: {},
            searchQuery: '',
            selected: {},
            recipes: []
        }
    },
    mounted() {
        UserInfoStore.load()
        this.userInfo = UserInfoStore.getToken()
        console.log('auth token:',UserInfoStore.getToken())
        axios.get('https://p4z8g78mya.execute-api.eu-west-2.amazonaws.com/prod/recipes', {
            headers: {
                'Authorization': UserInfoStore.getToken(),
                'Content-Type': 'application/json',
            },
        }).then(res => {
            this.recipes = res.data
        }).catch(err => {
            console.log('error', err)
        })
    },
}
</script>

<style scoped>
.scroll {
    overflow: scroll;
    height: 95vh;
}

.listitem {
    cursor: pointer;
    margin-bottom: 6px;
}

.listitem:hover {
    background-color: grey;
}

.search {
  margin: 5px;
  width: 100%;
}
</style>
